<template>
    <div>
        <!-- {{n}} -->
        <div class="cell" v-on:click="onClickSelf">
            <template v-if="z">{{text}}</template>
            <template v-else></template>
        </div>
    </div>
</template>

<script>
export default {
    props:["n",'finished'],
    data:()=>{
      //ES6 语法
      return{z:false,text:""};
    },
    // vue的高级特性，计算属性,text:"o"
    // computed:{
    //     text(){
    //         return this.n % 2 == 0 ? "x" : "o";
    //     }
    // },
    methods:{
        onClickSelf(){
            if(this.text != ''){
                return;
            }
            if(this.finished){
                return;
            }
            this.z = true;
            this.text = this.n % 2 == 0 ? "x" : "o";
            this.$emit('click',this.text);
        }
    }
}
</script>

<style>
.cell {
  border: 1px solid black;
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 33px;
}
</style>



